  right: 0px;
  display: block;
}
.menu:before{
	content: "";
	width:0; 
    height:50px; 
	position: absolute;
	top: 0px;
	left: -45px;
    border-top:115px solid transparent;
    border-right:45px solid #0066cb; 
}
