  position: absolute;
  top: 50%;
  left: 50%;
  margin:-10px 0 0 -60px;
  width: 105px;
  height: 30px;
  font-size: 31px;
  line-height: .9;
  font-weight: 800;
  color: #fff;
  text-align: right;
  cursor: pointer;
}
.btn-menu > span {
  position: absolute;
  left: 0;
  right: 0;
  width: 35px;
  height: 6px;
  background: #fff;
  -webkit-transition: .25s;
  transition: .25s;
  border-radius: 5px;
}
.btn-menu > span:nth-child(1) {
  top: 0px;
}
.btn-menu > span:nth-child(2) {
  top: 11px;
}
.btn-menu > span:nth-child(3) {
  top: 21px;
}
.btn-menu.cur span:nth-child(1) {
  top: 11px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.btn-menu.cur span:nth-child(2) {
  opacity: 0;
}
.btn-menu.cur span:nth-child(3) {
  top: 11px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
